"use client"
import { useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card"

// Sample data for schemes
const schemes = [
  {
    id: 1,
    department: "Healthcare",
    name: "Mahatma Jyotiba Phule Jan Arogya Yojana",
    eligibility: "Families under BPL",
    benefits: "Free treatment up to â‚¹5L",
    deadline: "31st Dec 2025",
    docs: ["Aadhaar Card", "Income Certificate", "Ration Card"],
    target: "all"
  },
  {
    id: 2,
    department: "Farmer",
    name: "Pradhan Mantri Fasal Bima Yojana",
    eligibility: "Registered farmers",
    benefits: "Insurance for crop loss",
    deadline: "15th July 2025",
    docs: ["Aadhaar Card", "Land Ownership Proof"],
    target: "farmer"
  },
  {
    id: 3,
    department: "Scholarship",
    name: "National Merit Scholarship",
    eligibility: "Students scoring above 80%",
    benefits: "â‚¹10,000 per year",
    deadline: "30th Sept 2025",
    docs: ["Aadhaar Card", "Marksheet", "School ID"],
    target: "student"
  },
  {
    id: 4,
    department: "Women & Child",
    name: "Beti Bachao Beti Padhao",
    eligibility: "Families with girl child",
    benefits: "Education & financial support",
    deadline: "Ongoing",
    docs: ["Aadhaar Card", "Birth Certificate"],
    target: "women"
  }
]

export default function CitizenDashboard() {
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [userProfile, setUserProfile] = useState<string>("all") // default: show all schemes

  useEffect(() => {
    const loggedIn = localStorage.getItem("isLoggedIn")
    const role = localStorage.getItem("role")
    const profile = localStorage.getItem("profile") || "all" // e.g., student/farmer/women/all

    if (loggedIn === "true" && role === "citizen") {
      setIsLoggedIn(true)
      setUserProfile(profile)
    } else {
      window.location.href = "/"
    }
  }, [])

  const handleLogout = () => {
    localStorage.clear()
    window.location.href = "/"
  }

  if (!isLoggedIn) return null

  // Filter schemes based on user profile
  const filteredSchemes = schemes.filter(
    (scheme) => scheme.target === "all" || scheme.target === userProfile
  )

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      {/* Header */}
      <header className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold text-blue-700">Citizen Dashboard</h1>
        <Button onClick={handleLogout} className="bg-red-600 hover:bg-red-700">
          Logout
        </Button>
      </header>

      <p className="mb-6 text-gray-700">
        Welcome Citizen ðŸ‘‹! Showing schemes relevant to your profile: <b>{userProfile}</b>
      </p>

      {/* Schemes List */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {filteredSchemes.map((scheme) => (
          <Card key={scheme.id} className="shadow-md hover:shadow-lg transition-shadow">
            <CardHeader>
              <CardTitle className="text-blue-700">{scheme.name}</CardTitle>
              <p className="text-sm text-gray-500">{scheme.department}</p>
            </CardHeader>
            <CardContent className="text-sm text-gray-600">
              <p><b>Eligibility:</b> {scheme.eligibility}</p>
              <p><b>Benefits:</b> {scheme.benefits}</p>
              <p><b>Deadline:</b> {scheme.deadline}</p>
              <p><b>Required Documents:</b> {scheme.docs.join(", ")}</p>
              <Button className="mt-3 bg-blue-600 hover:bg-blue-700">Apply Now</Button>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  )
}
